/* Spiner source code: https://codepen.io/CCG/pen/KrANmJ */

 li {
  list-style-type: none; /* Убираем маркеры */
 }

 .loader {
 	position: fixed;
 	top: 50%;
 	left: 50%;
 	width: 164px;
 	height: 164px;
 	margin: -80px 0px 0px -80px;
 	background-color: transparent;
 	border-radius: 50%;
 	border: 2px solid #E3E4DC;
 }

 .loader:before {
 	content: '';
 	width: 164px;
 	height: 164px;
 	display: block;
 	position: absolute;
 	border: 2px solid #898a86;
 	border-radius: 50%;
 	top: -2px;
 	left: -2px;
 	-webkit-box-sizing: border-box;
 	        box-sizing: border-box;
 	clip: rect(0px, 35px, 35px, 0px);
 	z-index: 10;
 	-webkit-animation: rotate infinite;
 	        animation: rotate infinite;
 	-webkit-animation-duration: 3s;
 	        animation-duration: 3s;
 	-webkit-animation-timing-function: linear;
 	        animation-timing-function: linear;
 }

 .loader:after {
 	content: '';
 	width: 164px;
 	height: 164px;
 	display: block;
 	position: absolute;
 	border: 2px solid #c1bebb;
 	border-radius: 50%;
 	top: -2px;
 	left: -2px;
 	-webkit-box-sizing: border-box;
 	        box-sizing: border-box;
 	clip: rect(0px, 164px, 150px, 0px);
 	z-index: 9;
 	-webkit-animation: rotate2 3s linear infinite;
 	        animation: rotate2 3s linear infinite;
 }

 .hexagon-container {
 	position: relative;
 	top: 33px;
 	left: 41px;
 	border-radius: 50%;
 }

 .hexagon {
 	position: absolute;
 	width: 40px;
 	height: 23px;
 	background-color: #556C82;
 }

 .hexagon:before {
 	content: "";
 	position: absolute;
 	top: -11px;
 	left: 0;
 	width: 0;
 	height: 0;
 	border-left: 20px solid transparent;
 	border-right: 20px solid transparent;
 	border-bottom: 11.5px solid #556C82;
 }

 .hexagon:after {
 	content: "";
 	position: absolute;
 	top: 23px;
 	left: 0;
 	width: 0;
 	height: 0;
 	border-left: 20px solid transparent;
 	border-right: 20px solid transparent;
 	border-top: 11.5px solid #556C82;
 }

 .hexagon.hex_1 {
 	top: 0px;
 	left: 0px;
 	-webkit-animation: Animasearch 3s ease-in-out infinite;
 	        animation: Animasearch 3s ease-in-out infinite;
 	-webkit-animation-delay: 0.21429s;
 	        animation-delay: 0.21429s;
 }

 .hexagon.hex_2 {
 	top: 0px;
 	left: 42px;
 	-webkit-animation: Animasearch 3s ease-in-out infinite;
 	        animation: Animasearch 3s ease-in-out infinite;
 	-webkit-animation-delay: 0.42857s;
 	        animation-delay: 0.42857s;
 }

 .hexagon.hex_3 {
 	top: 36px;
 	left: 63px;
 	-webkit-animation: Animasearch 3s ease-in-out infinite;
 	        animation: Animasearch 3s ease-in-out infinite;
 	-webkit-animation-delay: 0.64286s;
 	        animation-delay: 0.64286s;
 }

 .hexagon.hex_4 {
 	top: 72px;
 	left: 42px;
 	-webkit-animation: Animasearch 3s ease-in-out infinite;
 	        animation: Animasearch 3s ease-in-out infinite;
 	-webkit-animation-delay: 0.85714s;
 	        animation-delay: 0.85714s;
 }

 .hexagon.hex_5 {
 	top: 72px;
 	left: 0px;
 	-webkit-animation: Animasearch 3s ease-in-out infinite;
 	        animation: Animasearch 3s ease-in-out infinite;
 	-webkit-animation-delay: 1.07143s;
 	        animation-delay: 1.07143s;
 }

 .hexagon.hex_6 {
 	top: 36px;
 	left: -21px;
 	-webkit-animation: Animasearch 3s ease-in-out infinite;
 	        animation: Animasearch 3s ease-in-out infinite;
 	-webkit-animation-delay: 1.28571s;
 	        animation-delay: 1.28571s;
 }

 .hexagon.hex_7 {
 	top: 36px;
 	left: 21px;
 	-webkit-animation: Animasearch 3s ease-in-out infinite;
 	        animation: Animasearch 3s ease-in-out infinite;
 	-webkit-animation-delay: 1.5s;
 	        animation-delay: 1.5s;
 }

 @-webkit-keyframes Animasearch {
 	0% {
 		-webkit-transform: scale(1);
 		        transform: scale(1);
 		opacity: 1;
 	}

 	15%,
   50% {
 		-webkit-transform: scale(0.5);
 		        transform: scale(0.5);
 		opacity: 0;
 	}

 	65% {
 		-webkit-transform: scale(1);
 		        transform: scale(1);
 		opacity: 1;
 	}
 }

 @keyframes Animasearch {
 	0% {
 		-webkit-transform: scale(1);
 		        transform: scale(1);
 		opacity: 1;
 	}

 	15%,
   50% {
 		-webkit-transform: scale(0.5);
 		        transform: scale(0.5);
 		opacity: 0;
 	}

 	65% {
 		-webkit-transform: scale(1);
 		        transform: scale(1);
 		opacity: 1;
 	}
 }

 @-webkit-keyframes rotate {
 	0% {
 		-webkit-transform: rotate(0);
 		        transform: rotate(0);
 		clip: rect(0px, 35px, 35px, 0px);
 	}

 	50% {
 		clip: rect(0px, 40px, 40px, 0px);
 	}

 	100% {
 		-webkit-transform: rotate(360deg);
 		        transform: rotate(360deg);
 		clip: rect(0px, 35px, 35px, 0px);
 	}
 }

 @keyframes rotate {
 	0% {
 		-webkit-transform: rotate(0);
 		        transform: rotate(0);
 		clip: rect(0px, 35px, 35px, 0px);
 	}

 	50% {
 		clip: rect(0px, 40px, 40px, 0px);
 	}

 	100% {
 		-webkit-transform: rotate(360deg);
 		        transform: rotate(360deg);
 		clip: rect(0px, 35px, 35px, 0px);
 	}
 }

 @-webkit-keyframes rotate2 {
 	0% {
 		-webkit-transform: rotate(0deg);
 		        transform: rotate(0deg);
 		clip: rect(0px, 164px, 150px, 0px);
 	}

 	50% {
 		clip: rect(0px, 164px, 0px, 0px);
 		-webkit-transform: rotate(360deg);
 		        transform: rotate(360deg);
 	}

 	100% {
 		-webkit-transform: rotate(720deg);
 		        transform: rotate(720deg);
 		clip: rect(0px, 164px, 150px, 0px);
 	}
 }

 @keyframes rotate2 {
 	0% {
 		-webkit-transform: rotate(0deg);
 		        transform: rotate(0deg);
 		clip: rect(0px, 164px, 150px, 0px);
 	}

 	50% {
 		clip: rect(0px, 164px, 0px, 0px);
 		-webkit-transform: rotate(360deg);
 		        transform: rotate(360deg);
 	}

 	100% {
 		-webkit-transform: rotate(720deg);
 		        transform: rotate(720deg);
 		clip: rect(0px, 164px, 150px, 0px);
 	}
 }

 @-webkit-keyframes rotate3 {
 	0% {
 		-webkit-transform: rotate(0deg);
 		        transform: rotate(0deg);
 	}

 	100% {
 		-webkit-transform: rotate(360deg);
 		        transform: rotate(360deg);
 	}
 }

 @keyframes rotate3 {
 	0% {
 		-webkit-transform: rotate(0deg);
 		        transform: rotate(0deg);
 	}

 	100% {
 		-webkit-transform: rotate(360deg);
 		        transform: rotate(360deg);
 	}
 }



